.my-billing {
  max-width: 600px;
  margin: auto;

  img.card-logo {
    width: 50px;
  }

  input,
  .StripeElement {
    margin: 10px 0 20px 0;
    padding: 15px;
    border-radius: 5px;
    background-color: rgba(77, 154, 191, 0.1);
  }

  input:focus,
  .StripeElement--focus {
    box-shadow: 0 3px 9px rgba(50, 50, 9, 0), 3px 4px 8px rgba(82, 146, 176, 0.1);
    border: 1px solid #5292b0;
  }

  .card-list {
    ul {
      padding: 0;

      li {
        list-style-type: none;
        padding: 5px 0;

        span {
          font-weight: bold;
        }
      }
    }
  }
  .card-form {
    img {
      width: 50px;
    }
  }
  .card-number-head {
    position: relative;

    img {
      position: absolute;
      right: 10px;
      top: calc(100% + 20px);
    }
  }
}
